/******************** [ RESET(NOMALIZE) ] ************************/
html,body {height:100%;-webkit-text-size-adjust:none;font-family: "malgun gothic","Dothum";}
html,body,div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,address,big,cite,code,del,dfn,em,font,img,ins,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td {margin:0;padding:0;border:0 none}
h1,h2,h3,h4,h5,h6 {font-weight:normal;font-style:normal;}
li,select,input,label,button,img {vertical-align:middle}
th, em {font-weight:normal;text-decoration:none}
input,select,textarea {border-radius:0;}
input[type="text"],input[type="password"],input[type="file"],
input[type="tel"],input[type="number"],
input[type="email"],input[type="url"],textarea {-webkit-appearance:none;}
input[type="checkbox"],input[type="radio"] {
	margin:0;
}
img,fieldset,iframe {border:0 none}
table,div {border-collapse:collapse;border-spacing:0;}
ul,li,ol {list-style-type:none}
button,.button-fake {border:0 none;margin:0;padding:0;font-family:inherit;font-size:inherit;background:transparent;cursor:pointer}
button[disabled] {cursor:default}
button::-moz-focus-inner {border:0;padding:0}
table, textarea {width:100%;max-width:100%}
textarea {resize:none}
div,ul,li,span,input,button,.button-fake,a {box-sizing:border-box;-webkit-box-sizing:border-box;-moz-box-sizing:border-box;}
a,label {display:inline-block;vertical-align:middle}
span,strong {display:inline-block;vertical-align:middle}

a		{ color:#292929; cursor:pointer; }
a:hover { color:#04b0f8; text-decoration:underline; cursor:pointer; }


