}
a {
	text-decoration:none;
}
img {
	max-width:100%;
	height:auto;
}
/***** ( FORM ELEMENT ) *****/
input[type="text"],input[type="password"],
input[type="tel"],input[type="number"],
input[type="email"],input[type="url"],
input[type="file"],
select  {
	max-width:100%;
	font-family:inherit;
	font-size:inherit;
	border: 1px solid #b4bfc8;
	height: 25px;
	line-height:23px;
	padding: 0 5px;
	-webkit-appearance:none;
}
.table-result input[type="text"],.table-result input[type="password"], 
.table-result input[type="tel"],.table-result input[type="number"],
.table-result input[type="email"],.table-result input[type="url"],
.table-result input[type="file"],
.table-result select ,
.table-result textarea,
.table-primary input[type="text"],.table-primary input[type="password"], 
.table-primary input[type="tel"],.table-primary input[type="number"],
.table-primary input[type="email"],.table-primary input[type="url"],
.table-primary input[type="file"],
.table-primary select ,
.table-primary textarea  {
	border-color: #c5e2f3;
}
.table-none, .table-none tr, .table-none td	{
	padding:0 !important;
	margin:0 !important;
	border:0 !important;
}
.table-none .enter	{
	margin-right:4px;
}
input[type="text"][required],input[type="text"].req,
input[type="password"][required],input[type="password"].req,
input[type="tel"][required],input[type="tel"].req,
input[type="number"][required],input[type="number"].req,
input[type="email"][required],input[type="email"].req,
input[type="url"][required],input[type="url"].req,
input[type="file"][required],input[type="file"].req,
select[required],select.req {
	background-color: #d4f6ff;
}
input[type="text"][disabled],input[type="text"][readonly],
input[type="password"][disabled],input[type="password"][readonly],
input[type="tel"][disabled],input[type="tel"][readonly],
input[type="number"][disabled],input[type="number"][readonly],
input[type="email"][disabled],input[type="email"][readonly],
input[type="url"][disabled],input[type="url"][readonly],
input[type="file"][disabled],input[type="file"][readonly],
select[disabled],
textarea[disabled] { 
	background-color:#fff;
	color:#292929;
}
input[type="text"][disabled],
input[type="password"][disabled],
input[type="tel"][disabled],
input[type="number"][disabled],
input[type="email"][disabled],
input[type="url"][disabled],
input[type="file"][disabled],
select[disabled],
textarea[disabled] {
	border-color:#e1e5e9
}
input[type="text"]:focus,input[type="password"]:focus,
input[type="tel"]:focus,input[type="number"]:focus,
input[type="email"]:focus,input[type="url"]:focus,
input[type="file"]:focus,
select:focus,
textarea:focus  {
	background-color:#f9ffc5;
	outline:none;
}

input[type="text"][disabled]:focus,input[type="text"][readonly]:focus,
input[type="password"][disabled]:focus,input[type="password"][readonly]:focus,
input[type="tel"][disabled]:focus,input[type="tel"][readonly]:focus,
input[type="number"][disabled]:focus,input[type="number"][readonly]:focus,
input[type="email"][disabled]:focus,input[type="email"][readonly]:focus,
input[type="url"][disabled]:focus,input[type="url"][readonly]:focus,
input[type="file"][disabled]:focus,input[type="file"][readonly]:focus,
select[disabled]:focus,
textarea[disabled]:focus { 
	background-color:#f4f6f6;
}
select {
	min-width:50px;
	padding-right:26px;
	background:#fff url(../images/form/select-arrow.gif) right center no-repeat;
}
select::-ms-expand { display: none; }
select[disabled] {background-image:url(../images/form/select-arrow-dis.gif)} 
textarea {
	overflow:auto;
	min-height:25px;
	font-family:inherit;
	border: 1px solid #8ec5f4;
	box-sizing:border-box;
}
td textarea {vertical-align:middle}
input[disabled]+label , input[readonly]+label {cursor:default}
input[type].fake, textarea.fake  {
	outline:none;
	border-color:transparent;
	background-color:transparent;
	color:#292929;
	cursor:text;
	outline:none;
}

