/* ( lnb ) */
.lnbWrap {
	/* overflow:hidden; */
	position:absolute;
	top: 60px;
	bottom:0;
	width: 200px;
	padding-right: 10px;
    background-color:#1f303d;
    /* height:100vh; */
}
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
	.lnbWrap {
		bottom:100px;
	}
}
.lnbWrap .fakeBg {
	/* display:none; */
	position:absolute;
	top:0; 
	bottom:0;
/*	right:10px;*/
	width: 0;
	background-color:#fff;
/*	border-left: 1px solid #d0e4f6; */
	z-index: 40;
	transition: all ease .3s;
	opacity: 0;
}
.lnbWrap.fold .fakeBg {
	width: 204px;
    opacity: 1;
}
.lnbWrap:after {	
	display:block;content:"";
	position:absolute;top:0;bottom:0;right:0;
/*	width:10px;*/
/*	border-left:1px solid #d0e4f6;*/
	background-color: #fff;
	z-index: 2;
}
.lnb {
	width: 200px;
}
.lnb.fold h2, .lnb.fold ul li {
	white-space:nowrap;
}
.lnb h2 {
	height: 82px;
	font-size: 20px;
	font-weight:700;
	padding: 28px 16px 0;
	box-sizing: border-box;
/*	border: 1px solid #d0e4f6;*/
	border-bottom: 0;
    color:#fff;
    background-color:#1f303d;
    border-bottom: 1px solid #142029;
}
.lnb > ul {
	position:relative;
	z-index:10; 
    top:0px;
    width:200px;
    padding-left:4px;
}
.btn-lnb-fold {
	display:block;
	position:absolute; 
	right: 0px;
	top:0;
	height: 81px;
	width:24px;
/*	border: 1px solid #d0e4f6;*/
	z-index: 10;
	outline:none;
	transition: all linear .20s;
    background-color:#253b4d;
}
.lnbWrap.fold .btn-lnb-fold { 
	left:0;
	z-index: 50;
}
.lnbWrap.fold {
	width:26px;
	background-color:#eef0f1;
}
.lnb.fold.open,
.fold.lnbWrap .fakeBg {
	display:none;
	
}
.btn-lnb-fold:after,
.btn-lnb-fold:before {position:absolute;content:"";display:block;} 
.btn-lnb-fold:before {
	top:50%;
	left:50%;
	width:6px;
	height:10px;
	margin:-3.5px 0 0 -2px;
	background:url(../images/common/lnb-fold-close.gif) 0 0 no-repeat;
	z-index: 4; 
}
.lnbWrap.fold .btn-lnb-fold:before {background-image:url(../images/common/lnb-fold-open.gif)}
.btn-lnb-fold:after {
	left:0;top:0;bottom:0;right:0;
	background-color:#253d4d;
	transition:all ease .3s;
	z-index: 3;
}
/*.btn-lnb-fold:hover {border-color:#8ec5f4;}*/
.btn-lnb-fold:hover:after {background-color:#253d4d;}

.lnb .menu > li {
	position: relative;
	margin-top: -1px;
	z-index: 1;
}
.lnb .menu > li:first-child {margin-top: 0;}
.lnb .menu > li > a {
	display: block;
	padding: 13px 16px;
/*	border: 1px solid #d0e4f6;*/
	letter-spacing: -.7px;
	color: #fff; 
	transition:all ease .3s;
    background-color:#1f303d;
    /* width:200px; */
}
.lnb .menu > li.now,.lnb .menu > li:hover {z-index:2;}
.lnb .menu > li:hover > a,
.lnb .menu > li.now > a {
	background-color:#2f4e66;
/*	border-color:#ecf100;*/
/*     background-color: #e5f0fb;
    border-color: #8ec5f4; */
    color: #fff;
    border-left:4px solid #e05f09;
    /* margin-left:4px; */ /* border와 함께 생김*/
}
/* lnb 3dep */
.lnb .menu > li.menu-3 > a:after {
	display:block;content:"";
	position:absolute;right:14px;top:17px;
	width:7px;height:7px;
	background:url(../images/common/lnb-3dep-fold.gif) 0 0 no-repeat;
}
.lnb .menu > li.open > a:after {
	top:17px;
	background-image:url(../images/common/lnb-3dep-unfold.gif);  
}
.lnb .menu > li ul {
	overflow:hidden;
	height:0;
	padding-bottom:0;
	background-color:#4a718f;

	/*     background-color: #e5f0fb;
    border: 1px solid #8ec5f4; */
	border-top: 0;
	transition:all ease .3s;
	opacity: 0;
}
.lnb .menu > li.open ul {
	padding-bottom: 15px;
	border:1px solid #466175;
	border-top:0;
	opacity: 1;
}
.lnb .menu > li.open > a {
	border-bottom:0;
	padding: 13px 16px;
	border-bottom:1px solid #1f303d;
}
.lnb .menu > li li {
	padding-left: 26px;
	background: url(../images/common/lnb-3dep-bul.png) 15px no-repeat;
}
.lnb .menu > li li a {
	padding:6px 0;
	color:#fff;
}
.lnb .menu > li li.now a,
.lnb .menu > li li a:hover {
	text-decoration:underline;
	color:#fff;
}

/* ( workarea ) */
.workarea { 
	/* 	float:left; */
	position:relative;
	position:absolute;
    top:70px;
	left:210px;
	right:10px;
