/***** ( TAB ) *****/
.tabSec {
	overflow:hidden;
	margin:10px;
}
.tab-item {
	position: relative; 
	height:32px;
	z-index: 1;
	margin-bottom:-2px;
}
.tab-item li {
	position:relative;
	float:left;
	padding: 7px 15px 5px;
	margin-right:-1px;
	/* border: 1px solid #fff; */
	border-left: 1px solid #fff; 
	border-right: 1px solid #fff; 
	/*border-bottom: 1px solid #8ec5f4;*/ /* 선을 없애야지 왼쪽,오른쪽 흰 선들이 중간에 잘리지 않게 보임 */ 
	background-color: #9cc6e6;
	color: #000000;
	cursor: pointer; 
	z-index:0;
	transition:all linear .25s 
}
.tab-item:first-child {margin-left:-1px;}
.tab-item li:hover {
/*	background-color:#aedaff;*/
    background-color:#113957; /* 호버 했을 때 배경 색 블루 계열로 */
/*	border-color:#8ec5f4;  호버 했을 때 보더 색 없앰 */
	border-left: 1px solid #fff; 
	border-right: 1px solid #fff; 
	border-bottom-color:#aedaff;
	z-index:1;
    color:#fff;
}
.tab-item li.now {
