/* ( btn-search ) */
.inputSrh input[type=text] {
	max-width: 72%;
	max-width: calc(100% - 25px);
	border-right:0;
}
.inputSrh.full {width:100%}
.inputSrh.full input[type=text] {
	width: 72%;
	width: calc(100% - 25px);

}
.inputSrh input[type=text][disabled] + .btn-srh {
	background-color: #fafbfb;
	border-color: #e1e5e9;
	background-image:url(../images/form/btn-srh-dis.png) ;
	cursor:default;
	outline:none
}
button.btn-srh {
	width:24px; 
	height:25px;
	margin-left: -5px;
	border:1px solid #b4bfc8;
	border-left:0;
	background:#fff url(../images/form/btn-srh.png) 6px 5px no-repeat;
	transition:all ease .3s;
}
table .table-frame span.inputSrh button.btn-srh {
	margin-left: -5px;
}
.table-result .btn-srh,
.table-result .datepic button.date {border-color: #c5e2f3;}
.btn-srh:hover,.btn-srh:focus,
.datepic button.date:hover,.datepic button.date:focus {background-color: #f9ffc5;} 
/* ( btn-strong ) */
.btn-strong, .btn-normal {
	height:33px;
	line-height:33px;
	padding: 0 13px;
	border-radius: 3px;
	transition:all .5s ease;	 
}
.btn-strong {
	font-weight:700;
	background-color:#d14411;
	color: #fff;
}
/* ( btn-normal ) */
.btn-normal {
	background-color:#394a57;
	color:#fff;
}
.btn-strong[disbled], .btn-normal[disabled] {
	background-color:#e1e5e9;
	color:#f6f8f9
}
.btn-strong:hover,.btn-normal:hover {background-color:#000}
/* ( btn-sub ) */ 
.btn-sub,.button-fake {
	height: 25px;
	padding-right: 11px;
	padding-left:8px;
	border:1px solid #8ec5f4;
	background-color:#f1f8ff;
	border-radius:2px;
	letter-spacing:-1px;
	color:#0658a0;
	transition:all linear .25s;
	font-size: 13px;
	/* vertical-align: top; */
} 
/* ( btn-sheet ) */ 
.btn-sheet {
	height: 25px;
	/* padding: 0 11px 3px; */
	padding-right: 11px;
	padding-bottom: 3px;
	background-color: #1a5380;
	/* background-color: #f1f8ff;
	border: 1px solid #8ec5f4; */
	border-radius: 2px;
	letter-spacing: 0px;
	color:#fff;
	/* color: #0658a0; */
	transition:all linear .25s;
	font-size: 13px;
	/* margin-right:10px; */
	padding-left:11px;

}
.btn-style{
	height: 25px;
	/* padding: 0 11px 3px; */
	padding-right: 11px;
	padding-bottom: 3px;
	background-color: #f1f8ff;
	border: 1px solid #8ec5f4;
	border-radius: 2px;
	letter-spacing: 0px;
	color: #0658a0;
	transition:all linear .25s;
	font-size: 13px;
	margin-right:10px;
	padding-left:11px;
	margin-top:25px;
}
.btn-sheet:hover, .btn-sub:hover {
	background-color:#aedaff;
}
.btn-sheet[disabled],.btn-sub[disabled] {
	border-color:#d8dce0;
	background-color:#e1e5e9;
