.popup-body	{min-width:initial;}
.popup-wrap {	
	background-color:#e5f0fb;
	z-index: 450;
	width:100%;
	height:100%;
}
.popup-wrap.layer {
	border: 5px solid #6daeea;
	display:none;
	position:absolute;
	left:50%;
	top:50%;
	/* max-width: 80%; */
}
/* ( popup-head ) */
.popup-head {
	position:absolute;top:0;right:0;left:0;
	height:45px;
	padding-top:1px;
	border-bottom:1px solid #d0e4f6;
	background-color:#1f303d;
	z-index: 10;
}
.popup-head h1 {
	padding-top:7pt;padding-left:20px;
	font-size:13pt;
	font-weight:700;
	color:#fff;
}
.pop-close {
	position:absolute;
	right:0;
	top: 0;
	width: 51px;
	height:44px;
	font-size:0;
	line-height:0;
	text-indent:-999px;
	background:url(../images/popup/popup-close.png) center center no-repeat;
}
/* ( popup-cont ) */
.popup-cont {
	position: absolute;
	top: 41px;
	left: 0;
	right: 0;
	bottom: 0;
	overflow-y:auto;
	padding: 20px 22px 0;
}
.popup-cont:after {
	display: block;
	content:"";
	height: 20px;
	width:100%;
}
.popup-mask {
	display: none;
	position:fixed; 
	top:-20px;
	left:-100px;
	bottom:-1000px;
	right:0;
	background:url(../images/popup/popup-mask.png) 0 0 repeat;
	z-index: 400;
}
/* (popup-btns) */
.popup-btns {
	margin-top:20px;
	text-align:center;
}
.btn-pop {
	height:27px;
	padding: 0 12px 1px; 
	border-radius:3px;
	background-color:#2768b1;
	color:#eef3f9;
}
/* popup form design */
.popup-wrap .srcForm>  table  {table-layout:fixed}
