/******** MAIN ********/
table.grid-wrap	{
	min-width:1245px;
}
table.grid-wrap td {
	padding-left: 20px;
	padding-bottom:20px;
	width:30%;
	vertical-align:top;
}
table.grid-wrap td:first-child {
	padding-left:30px;
}
table.grid-wrap td:last-child {
	padding-right:30px;
}
table.grid-wrap .space {
	height:70px;
	padding:0;
} 
/* ( main-board ) */
.grid-wrap .board { 
	width: 100%;
	height:270px;
	padding: 10px;
	border: 1px solid #6daeea;
	transition:all ease .35s;
	}
/* ( main-board-title ) */
.grid-wrap .board .title {
	padding: 0 10px; 
	margin: -10px -10px 15px -10px;
	height:32px;
	background-color:#dbedfd;
	border-bottom: 1px solid #bcd5f2;
}
.grid-wrap .board .title:after {
	content:""; display:block; clear:both;
}
.grid-wrap .board .title h3 {
	float:left;
	font-weight:700;
	font-size:13px;
	padding-top: 8px;
	letter-spacing:-.25px;
	color:#154f8e;
}
.grid-wrap .board .title .viewmore { 
	float:right;
}
.grid-wrap .board .title .viewmore button {
	background:url(../images/bullet/ic-viewmore.png) center center no-repeat;
	width:30px;
	height:23px;
	margin-top:4px;
}
.grid-wrap .board ul li	{
	height:27px;
	list-style-type:disc;
	margin-left:20px;
}
.grid-wrap .board ul li.date	{
	height:17px;
	list-style-type:none;
	color:#999;
	font-size:11px;
}
.ic-new	{
	width:11px;
	height:11px;
	background:url(../images/bullet/ic-new.gif)no-repeat;
	margin-left:5px; 
}



/******** Statistics MAIN ********/
